<template>
  <div class="page" id="service-item">
    <div class="content">
      <img :src="serviceObject.bannerImg" alt="">
      <div class="p15 f14 bgc-white td" v-if="serviceObject.content" v-html="serviceObject.content"></div>
      <div>
        <ServiceItem :showServiceName="serviceObject.serviceItem"/>
      </div>
    </div>
  </div>
</template>
<script>
  const serviceParent = [{
    name: '全球产业视野',
    serviceItem: ['空间开发服务', '多功能公关服务平台'],
    content: '领先的企业都有明确的创新重点，依托星河及产业联盟会强大的行业资源，持续发掘行业竞争优势，转化为可以参照的知识系统，转变企业发展观念及驱动其不断创新，向价值链更高端迈进。'
  }, {
    name: '政企对接平台',
    serviceItem: ['高级政务服务'],
    content: '联盟会员有着丰富的行业发展经验，但在跨区域发展乃至全国布局时会遇到瓶颈。而企业走出去想要稳健经验，需要注重当地的政策环境、营商环境、人才环境等多重复杂因素。我们能够借助在各大城市落地载体建设的综合影响力，帮助摆脱被动选址的角色，主动参与到与各地各政府机构的积极沟通中来，切实实现政府扶持与企业需求的紧密结合。'
  }, {
    name: '产融投资',
    serviceItem: ['创新金融服务'],
    content: '新兴技术往往是驱动企业领先的动力，最成功的企业积极拥抱新的技术，但需要加入资本作为产业化的催化剂，依托载体资源发现企业新的增长机遇以及建立完整的投后服务体系，以投融资的模式服务创新产业，共同分享企业发展的红利，构建起完整的推动企业发展的全生命周期服务体系。'
  }, {
    name: '产业服务输出',
    serviceItem: ['咨询顾问服务', '产业政策服务', '知识产权规划及资质管理服务', '技术及人才服务', '培训及教育服务', 'O2O服务平台'],
    content: '促进企业非核心业务外包，多维度整合各项产业服务资源，为联盟成员提供人才招聘、品牌推广、空间定制等一站式商务办公解决方案，让企业关注核心业务发展，实现更好更快成长壮大。'
  }, {
    name: '全球资源导入',
    serviceItem: [],
    content: '我们的目标是让企业抓住全球市场增长的机遇，寻找具备全球竞争力的最佳伙伴或者是能够提供补充企业发展动力的服务者。价值链的分工的复杂性驱使企业必须与具备产业资源的组织开展联动，才能有针对性地寻求到无论是业务、资源、资金，还是技术方面的合作同盟。'
  }]
  import * as utils from '@/common/utils.js'
  import ServiceItem from './service-grid.vue'
  import bannerImg1 from '@/assets/images/industryUnion/pic_views.png'
  import bannerImg2 from '@/assets/images/industryUnion/pic_gov.png'
  import bannerImg3 from '@/assets/images/industryUnion/pic_invest.png'
  import bannerImg4 from '@/assets/images/industryUnion/pic_export.png'
  import bannerImg5 from '@/assets/images/industryUnion/pic_import.png'

  export default {
    name: 'serviceItem',
    data() {
      return {
        serviceObject: {}
      }
    },
    created() {
      const serviceName = decodeURIComponent(utils.getQueryString(window.location.href, 'serviceName')) || ''
      if (!serviceName) {
        window.location.hash = ''
      }
      const serviceObject = serviceParent.find(data => data.name === serviceName)
      switch (serviceName) {
        case '全球产业视野':
          serviceObject.bannerImg = bannerImg1;
          break;
        case '政企对接平台':
          serviceObject.bannerImg = bannerImg2;
          break;
        case '产融投资':
          serviceObject.bannerImg = bannerImg3;
          break;
        case '产业服务输出':
          serviceObject.bannerImg = bannerImg4;
          break;
        case '全球资源导入':
          serviceObject.bannerImg = bannerImg5;
          break;
      }
      this.$store.dispatch('UPDATE_LOADINGSTATE', false)
      this.serviceObject = serviceObject
      document.title = serviceName
    },
    components: {
      ServiceItem
    },
    methods: {}
  }
</script>
<style lang="scss">
  @import '../../../assets/scss/Common/variables.scss';

  #service-item .service-item:last-child {
    border-right: none;
  }
</style>
